  font-family: "Nunito";
  src: url("./Fonts/Nunito-Bold.ttf") format("truetype"),
  url("./Fonts/Nunito-Bold.ttf") format("truetype");
  font-weight: 600;
  font-display: swap;
}

@font-face {
  font-family: "Nunito";
  src: url("./Fonts/Nunito-ExtraBold.ttf") format("truetype"),
