* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* font-family: "Poppins", Arial, sans-serif; */
  /* font-family: "Nunito", sans-serif; */
  /* font-family: "Nunito-Regular", sans-serif; */
  font-family: "Nunito", sans-serif;
  /* font-family: "Nnto", sans-serif; */
  font-weight: 500;
  /* font-weight: 200 !important; */
  /* font-family: "Montserrat", sans-serif; */
}

:root {
  --green_v1: #229e56;
  --green_v1_hover: #219150;
  --grey: #464646;
  --greye: #5d5d5d;
  --greyer: #909090;
  --greyerer: #c2c2c2;
}

body {
  min-width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

button:focus {
  outline: 0;
}

html {
  /* scroll-behavior: smooth; */
  overflow-x: hidden;
}
