@mixin lower-then-desktop {
  @media (max-width: 900px) {
    @content;
  }
}

.nav-wrapper {
  background: #f9f9f9;
  // background: #f9f9f957;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  // box-shadow: 4px 4px 10px #adadad5e, -2px -2px 14px #d1d1d156;
  box-shadow: 2px 2px 10px rgba(224, 224, 224, 0.63);
  position: relative;
  z-index: 5;
  position: fixed;
  top: 0;
  width: 100%;
  transition: 0.3s ease-out;
  padding: 0 15% 0 15%;
  white-space: nowrap;
  // border: 1px solid green;
  @include lower-then-desktop {
  }
  .burgerDiv {
    right: 10%;
    position: absolute;
    height: 35px;
    width: 45px;
    transition: all 0.3s ease;
    transform: scale(0.7);
    display: none;
    // border: 1px solid red;

    .line1 {
      position: absolute;
      top: 0;
      width: 100%;
      height: 4px;
      background: black;
      border-radius: 20px;
      transition: 0.4s ease-in-out;
    }
    .toggled1 {
      transform: rotate(225deg);
      top: calc(50% - 4px);
    }
    .line2 {
      width: 100%;
      height: 4px;
      background: black;
      border-radius: 20px;
      transition: 0.4s ease-in-out;
    }
    .toggled2 {
      width: 0;
      height: 0;
      margin-left: 100px;
    }

    .line3 {
      position: absolute;
      width: 100%;
      height: 4px;
      background: black;
      border-radius: 20px;
      transition: 0.4s ease-in-out;
      bottom: 0;
    }
    .toggled3 {
      position: absolute;
      transform: rotate(-45deg);
      transform: rotate(-225deg);
      bottom: calc(50%);
    }

    @include lower-then-desktop {
      display: flex;
      transition: 0.5s ease-in-out;
      align-items: center;
      justify-content: space-evenly;
      flex-direction: column;
    }
    // .burger {
    //   height: 70%;
    //   transition: 0.5s ease-in-out;
    //   font-size: 50px;
    // }
  }

  .myName-as-logo {
    overflow: visible;
    position: relative;
    cursor: pointer;
    transition: 0.3s ease-out;
    // border: 1px solid red;
    display: flex;
    align-items: center;
    // margin-left: -20px;
    transform: translateX(-40px);

    .logoImg {
      transition: 0.3s ease-out;
      // background: var(--green_v1);
      // width: 3px;
      // height: 100%;
      // position: absolute;
      // // left: 0;
      // overflow: hidden;
      // height: 100%;
      // width: 100%;
      // border: 2px solid var(--green_v1);
      // border: 1px solid brown;
      @include lower-then-desktop {
        transform: scale(0.8);
        margin-left: 6px;
      }
      img {
        height: 40px;
      }
    }

    .nameText {
      width: 100%;
      height: 100%;
      color: black;
      // margin-left: 25px;
      padding-left: 25px;
      letter-spacing: 2px;
      transition: 0.3s ease-out;
      font-size: 17px;
      // overflow: visible;
      // border: 1px solid salmon;
      // position: relative;
      @include lower-then-desktop {
        font-size: 11px;
        padding-left: 15px;
      }
      &::before {
        content: "";
        position: absolute;
        top: 0px;
        bottom: 0px;
        background: var(--green_v1);
        width: 3px;
        height: 100%;
        margin-left: -20px;
        // border:1px solid red;

        // margin-right: -20px;
        @include lower-then-desktop {
          // top: 8px;
          // bottom: 8px;
          margin-left: -15px;
        }
      }
    }
  }

  .nav-links {
    width: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
    font-size: 1.2rem;
    transition: 0.3s ease-out;

    @include lower-then-desktop {
      width: 50%;
      padding: 10px;
      // border: 1px solid red;
      flex-direction: column;
      position: absolute;
      top: 10vh;
      background: #f9f9f9;

      box-shadow: -4px 4px 7px #adadad36;
      box-shadow: -6px 6px 10px -10px #5f5f5f49;
    }

    .lomitko {
      padding: 0px 5px;
      @include lower-then-desktop {
        padding: 0px 3px;
      }
    }
    li {
      cursor: pointer;
      transition: 0.3s ease-in-out;
      // border: 1px solid red;
      padding-top: 3%;
      padding-bottom: 3%;
      position: relative;

      @include lower-then-desktop {
        width: 100%;
        text-align: center;
        // background: red;
        font-size: 0.9rem;
        padding: 15px;
        margin-right: 5px;
      }
    }

    li.toggleButton {
      position: absolute;
      right: 30px;
      top: 0;
      height: 100%;
      display: flex;
      padding: 0;
      align-items: center;
      // border:2px solid cyan;
      transition: 0.3s;
      cursor: inherit;
      // margin-right: 200px;
      @include lower-then-desktop {
        bottom: 0;
        position: relative;
        width: 100%;
        right: 0;
        display: flex;
        justify-content: center;
        border-top: 1px solid grey;
        margin-top: 15px;
        padding-top: 15px;
        padding-bottom: 15px;
        transform: scale(1);
      }
      span.before {
        margin-right: 7px;
      }
      span.after {
        margin-left: 7px;
      }

      input[type="checkbox"] {
        height: 0;
        width: 0;
        visibility: hidden;
      }

      label {
        cursor: pointer;
        // border: 1px solid red;
        text-indent: -9999px;
        width: 45px;
        height: 22px;
        background-image: url("../images/Flag_of_the_United_Kingdom.svg");
        opacity: 0.8;
        background-position: center;
        background-size: cover;
        display: block;
        border-radius: 100px;
        position: relative;
      }

      label:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0.9px;
        width: 22px;
        height: 22px;
        background: white;
        box-shadow: inset 0px 0px 5px 1px rgba(0, 0, 0, 0.5);

        border-radius: 90px;
        transition: 0.3s;
      }

      input:checked + label {
        // background: #bada55;
        background: url("../images/Flag_of_Slovakia.svg");
        background-position: center;
        background-size: cover;
      }

      input:checked + label:after {
        left: calc(100% - 0.9px);
        transform: translateX(-100%);
      }

      label:active:after {
        width: 28px;
      }
    }
  }
  .square {
    background: rgba(255, 255, 255, 0.3);
    background-color: blue;
    position: absolute;
    height: 90vh;
    left: 0;
    width: 17%;
    transform: skew(-55deg);
    margin-left: 1.1%;
    z-index: 0;
    top: 0;
  }
}

.sticky {
  height: 6vh;
  transition: 0.3s ease-in-out;
  box-shadow: 4px 4px 4px #c0c0c046, -2px -2px 8px #d6d6d63d;

  .myName-as-logo {
    transform: scale(0.8) translateX(-62px);
    .logoImg {
      @include lower-then-desktop {
        margin-left: 2px;
      }
    }
  }

  .nav-links {
    top: 6vh;
    li:not(:last-child) {
      font-size: 0.8rem;
    }

    li.toggleButton {
      transform: scale(0.8);
    }
  }

  .burgerDiv {
    transform: scale(0.5);
    .burger {
      transform: scale(1.1);
    }
  }
}
