@mixin lower-then-desktop {
  @media (max-width: 900px) {
    @content;
  }
}
// @mixin lower-then-desktop {
//   @media (max-width: 900px) {
//     @content;
//   }
// }

$bor: 0px;

.mainPage {
  border: 0.001px solid rgba(255, 255, 255, 0);
  // margin-top: 10vh;
  max-width: 100vw;
  height: 100vh;
  // min-height: 100vh;
  // max-height: 150vh;
  background-color: #ebebeb9b;
  position: relative;
  // background: radial-gradient(
  //   circle at 72.5% 45.5%,
  //   rgba(248, 248, 248, 1) 0%,
  //   rgba(231, 231, 231, 1) 100%
  // );
  background: radial-gradient(
    circle at 72.5% 45.5%,
    rgba(251, 251, 251, 1) 0%,
    rgb(240, 240, 240) 100%
  );
  overflow: hidden;
  // overflow: hidden;
  // border: 2px solid red;
  border-bottom: 1px solid white;
  @include lower-then-desktop {
    padding-bottom: 30px;
    // height: min-content;
    min-height: 100vh;
  }

  .waves {
    // bottom: -7.7px;
    // left: 20.4%;
    // left: 384px;
    // bottom: 0;
    // width: 100%;
    // position: relative;
    // border: 5px solid red;
    // height: 100%;
    // z-index: -999;
    img {
      width: 100%;
      // width: 100vw;
      left: 0;
      position: absolute;
      bottom: -3px;
      // border: 10px solid green;
      @include lower-then-desktop {
        // width: 0;
        transform: scaleY(1.4) translateY(-13px);
      }
    }
  }

  .squareMainScreen {
    background: rgba(255, 255, 255, 0.267);
    position: absolute;
    height: 80vh;
    margin-top: -5vh;
    left: 10vh;
    width: 18%;
    transform: skew(-55deg);
    z-index: 5;
    top: 0;
  }

  .main-content {
    font-size: 35px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 15%;
    margin-right: 15%;
    height: 100%;
    position: relative;
    z-index: 4;
    // border: 2px solid green;

    @include lower-then-desktop {
      margin-left: 5%;
      margin-right: 5%;
      height: 90vh;
      margin-top: 10vh;
      display: block;
    }

    .my-stuff {
      color: white;
      font-weight: normal;
      // max-width: 700px;
      letter-spacing: 0.7px;
      // border: 2px solid blue;
      // height: auto;
      // width: auto;
      div:nth-child(1) {
        font-size: 30px;
        color: rgba(39, 39, 39, 0.658);
        line-height: 1;

        @include lower-then-desktop {
          font-size: 18px;
          line-height: 2;
          padding-top: 20px;
        }
      }

      div:nth-child(2) {
        font-size: 50px;
        font-weight: 600;
        line-height: 1.3;
        color: black;
        margin-bottom: 10px;
        @include lower-then-desktop {
          font-size: 28px;
          line-height: 1.3;
          letter-spacing: 0.4px;
        }
      }

      div:nth-child(3) {
        font-size: 16px;
        color: rgb(233, 233, 233);
        line-height: 2;
        font-weight: normal;
        color: rgb(219, 219, 219);
        // border: 1px solid blue;
        color: rgb(109, 109, 109);
        @include lower-then-desktop {
          font-size: 14px;
          // line-height: 1.7em;
        }
      }
    }

    .myImage-main {
      position: relative;
      // top: 0;
      // margin: 0;
      // display: inline-block;
      // padding: 0;
      display: flex;
      justify-content: center;
      width: 100%;
      margin-top: -10px;
      // overflow: visible;
      // border: 2px solid red;
      @include lower-then-desktop {
      }

      .imgSmallContainer {
        // border: 2px solid cyan;
        position: relative;
        // padding-top: 50px;
        // width: 20%;
        @include lower-then-desktop {
          margin-top: -70px;
          transform: scale(0.8);
        }
        .htmlChanging {
          position: absolute;
          top: 20%;
          left: 4%;
          width: 39%;
          // width: 140px;
          text-align: center;
          color: var(--green_v1);
          // font-size: 1.6rem;
          font-size: 1.6vw;
          // font-size: 100%;
          // font-size: 25.5vw;
          white-space: nowrap;
          // border: 1px solid brown;
          @include lower-then-desktop {
            // top: 10%;
            font-size: 3.6vw;
            // left: 3%;
            // width: 37%;
          }
          // border: 2px solid red;
        }
        img {
          // width: 52%;
          // height: 52%;
          // min-width: 300px;
          // max-width: 560px;

          width: 100%;
          height: 100%;
          width: 31vw;
          height: auto;
          // margin-left: 300px;
          // border: 2px solid red;
          // box-shadow: 20px 20px 0px 0px rgba(207, 207, 207, 0.4);
          // border-radius: 50%;

          // margin-right: 40px;

          @include lower-then-desktop {
            width: 70vw;
            max-width: 50vh;
            // min-width: 190px;
            // min-width: 170px;
            // width: 40vw;
            // margin-top: -40px;
            // box-shadow: 10px 10px 0px 0px rgba(207, 207, 207, 0.4);
          }
        }
      }
    }

    .button-wrapper {
      display: flex;
      margin-top: 40px;
      @include lower-then-desktop {
        margin-top: 25px;
      }
      button {
        border-radius: 20px;
        font-size: 19px;
        width: 180px;
        height: 48px;
        border: none;
        transition: 0.2s;
        margin-top: -10px;

        @include lower-then-desktop {
          // transform: scale(0.7);
          // justify-content: space-evenly;
          width: 35%;
          max-width: 180px;
          height: 38px;
          white-space: nowrap;
          border-radius: 16px;
          // font-size: 16px;
          font-size: 0.9rem;
          // border: 5px solid red;
        }
      }

      button:hover {
        cursor: pointer;
      }

      button.my-projects-button {
        background: var(--green_v1);
        color: white;
      }

      button.my-projects-button:hover {
        background: #219150;
        border-radius: 15px;
      }

      button.about-button {
        color: #696969;
        color: var(--green_v1);
        border: 3px solid var(--green_v1);
        background: transparent;
        margin-left: 60px;
        @include lower-then-desktop {
          margin-left: 30px;
        }
      }

      button.about-button:hover {
        border: 3px solid #219150;
        color: #219150;
        background-color: rgba(255, 255, 255, 0.171);
        border-radius: 15px;
      }
    }

    .line-underButtons {
      margin-top: 30px;
      background: rgba(184, 184, 184, 0.678);
      width: 210px;
      height: 2px;
      margin-bottom: 10px;
      @include lower-then-desktop {
        margin-top: 25px;
      }
    }
  }
}
